.title{
  text-align: center;
}
.contact-card{
  width:  300px;
  height:  500px;
  box-shadow: 1px 1px 8px #c5c2c2;
  margin: 20px;
}

.contact-card-img-parent{
  padding: 25px;
  width: 250px;
  height: 250px;
}

.contact-card-img{
  border-radius: 5px;
  box-shadow: 1px 1px 8px #c5c2c2;
}

p{
  margin: 10px 25px 0;
}
.contact-card .contact-name{
  margin-top: 0;
}

.icons{
  color: rgb(127, 125, 130);
  margin-right: 10px;
  font-size: 15px;
}

.cards{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;
  flex-direction: row;
}